import Data from './contact.json';
//import Data from '../db.json'

const Contact = () => {
    return(
        <div id="contact" className="my-5 py-5">
            <div className="container ps-sm-3 ps-md-5 text-center text-sm-start">
                <div className="text-title">Follow me</div>
                <div className="row pt-5 pt-sm-4">
                    {Data.map((contact) => (
                        <div key={contact.id} className="col-4 col-sm-6 mb-5 mb-sm-4">
                            <a href={contact.link}><i className={contact.icon}></i> <span className="d-none d-sm-inline ms-4">{contact.nama}</span></a>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    )
}

export default Contact;